
.container-fluid {
    background-color: #e6ffff;
    background-image: url("/assets/background.JPG");
}
.title-bar {
    width: 100%;
    background-color: #00e6e6;
    padding: 1%;
}

.title {
    font-family: 'Fontdiner Swanky', cursive;
    text-align: center;
    padding-top: 1%;
}

.star {
    width: 300px;
}

h1 {
    text-align: center;
}
.img-container {
    width: 140px;
    height: 200px;
    display: inline-block;
    border-style: outset;
    margin: 10px;
    background-color: #ffffcc;
}

img {
    width: 120px;
    height: 180px;
}


.card-header {
    background-color: #33d6ff;
    font-family: 'Fontdiner Swanky', cursive;
}

.card-body {
    background-color: #ffffcc;
    font-family: 'Fontdiner Swanky', cursive;
}
